#wrapper {
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8px;
  margin: 10px;
  border: 3px solid blue;
  border-radius: 10px;
}

.card-bottom-row {
  display: flex;
  flex-direction: row;
}

#cardBox {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

span {
  padding: 0px 10px;
}

#start-stop, #reset {
  width: 100px;
  margin: 0px 10px;
}